import SelectInput from 'components/input/SelectInput';
import TextIconInput from 'components/input/TextIconInput';
import React from 'react';
import { Button, Form, Row } from 'react-bootstrap';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import CheckboxInput from 'components/input/CheckboxInput';
import { useFormik } from 'formik';
import { finishYearOption } from 'components/helper/helper';
interface Props {
  goBackStep: (value: number) => void;
}

const FormAuthPhoneNumber = (props: Props) => {
  const formik = useFormik({
    initialValues: {
      phone: '',
      country_code: '',
      receive_sms: '',
    },
    onSubmit: async (values) => {
      console.log(values);
    },
  });
  return (
    <>
      <Form onSubmit={formik.handleSubmit}>
        <Row className="m-3">
          <SelectInput
            mdCol={'4'}
            values={formik.values.country_code}
            onChange={formik.handleChange}
            name={'country_code'}
            label={'Country Code'}
            option={finishYearOption}
          />

          <TextIconInput
            mdCol={'6'}
            icon={<PhoneAndroidIcon />}
            label={'Phone Number'}
            name={'phone'}
            values={formik.values.phone}
            onChange={formik.handleChange}
          />
        </Row>
        <CheckboxInput
          mdCol={'6'}
          label={<span className="mt-2">I would like to receive SMS updates</span>}
          name={'receive_sms'}
          values={formik.values.receive_sms}
          onChange={formik.handleChange}
        />
        <Button className="w-25 p-3 m-3" variant="primary" onClick={() => formik.handleSubmit()}>
          <span className="h4">Send Code</span>
        </Button>
      </Form>
    </>
  );
};

export default FormAuthPhoneNumber;
