import SelectInput from 'components/input/SelectInput';
import React from 'react';
import { Row, Col, Image, Button, Container, Badge } from 'react-bootstrap';
import FormAuthPhoneNumber from './FormAuthPhoneNumber';
import FormRegister from './FormRegister';

interface Props {}

const RegisterPage = (props: Props) => {
  const [step, setStep] = React.useState(1);
  return (
    <Row className="vh-100 vw-100">
      <Col
        xs={0}
        md={4}
        className="h-100 d-none d-md-flex flex-wrap justify-content-center align-items-center bg-primary "
      >
        <div className="w-100">
          <Image src="/assets/images/login-left.png" width="100%" />
        </div>
      </Col>
      <Col xs={12} md={8} className="d-flex h-100 align-items-center">
        <Container>
          <div className="d-flex p-2 mb-5 justify-content-between align-items-center">
            <span>
              <SelectInput mdCol={'12'} option={[{ name: 'English (USA)', id: 1 }]} />
            </span>
            <span>
              Already a member ?<Button variant="link">Login</Button>
            </span>
          </div>
          {step === 1 && (
            <>
              <h2 className="m-3">🎓 Let's set you up!</h2>
              <div className="m-3 d-flex justify-content-between align-items-center">
                <h5 className="m-3">Join us in a easy two-step account creation</h5>
                <Badge className="px-2 py-1" bg="secondary">
                  <span className="h6">{step}/2</span>
                </Badge>
              </div>
              <div className="m-3">
                <FormRegister onNextStep={setStep} />
              </div>
            </>
          )}
          {step === 2 && (
            <>
              <div>
                <Button className="text-muted" variant="light" onClick={() => setStep(1)}>
                  Back
                </Button>
              </div>
              <h2 className="m-3">Confirm your phone number</h2>
              <div className="m-1 d-flex justify-content-between align-items-center">
                <h5 className="m-3">We will send you an SMS with a 4 verification code.</h5>
                <Badge className="px-2 py-1" bg="secondary">
                  <span className="h6">{step}/2</span>
                </Badge>
              </div>
              <div className="m-3">
                <FormAuthPhoneNumber goBackStep={setStep} />
              </div>
            </>
          )}
        </Container>
      </Col>
    </Row>
  );
};

export default RegisterPage;
