import PasswordInput from 'components/input/PasswordInput';
import TextIconInput from 'components/input/TextIconInput';
import SelectInput from 'components/input/SelectInput';
import { useFormik } from 'formik';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import React from 'react';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import { Form, Row, Button, Card, CloseButton } from 'react-bootstrap';
import {
  finishYearOption,
  mediumPasswordRegexp,
  strongPasswordRegexp,
} from 'components/helper/helper';
import GoogleLogin from 'react-google-login';
import CheckboxInput from 'components/input/CheckboxInput';
import { useToasts } from 'react-toast-notifications';

interface Props {
  onNextStep: (step: number) => void;
}

interface passwordStrengthProps {
  title: string;
  content: string;
  status: string;
}

const FormRegister = (props: Props) => {
  const { addToast } = useToasts();
  const [passwordStrength, setPasswordStrength] = React.useState<passwordStrengthProps | undefined>(
    undefined
  );
  const formik = useFormik({
    initialValues: {
      email: '',
      first_name: '',
      last_name: '',
      highschoolGraduation: '',
      password: '',
      password_confirm: '',
    },
    onSubmit: async (values) => {
      console.log(values);
      if (values.password_confirm !== values.password) {
        addToast('Your password and confirmation password do not match', {
          appearance: 'error',
          autoDismiss: true,
        });
        return;
      }
      props.onNextStep(2);
    },
  });

  React.useEffect(() => {
    if (formik.values.password.length > 1) {
      if (strongPasswordRegexp.test(formik.values.password)) {
        setPasswordStrength({
          title: 'Strong Password',
          content: 'Great! This would do, now just don’t forget it.',
          status: 'text-success',
        });
      } else if (mediumPasswordRegexp.test(formik.values.password)) {
        setPasswordStrength({
          title: 'Medium Password',
          content:
            'This password look good. Add one lowercase letter , one uppercase letter , one digit , one special character for stronger.',
          status: 'text-warning',
        });
      } else {
        setPasswordStrength({
          title: 'Weak Password',
          content: 'You password need at least six characters.',
          status: 'text-danger',
        });
      }
    }
  }, [formik.values.password]);
  return (
    <Form onSubmit={formik.handleSubmit}>
      <Row className="mb-1">
        <TextIconInput
          icon={<PersonOutlineIcon />}
          mdCol={'6'}
          label={'First name'}
          name={'first_name'}
          values={formik.values.first_name}
          onChange={formik.handleChange}
        />
        <TextIconInput
          icon={<PersonOutlineIcon />}
          mdCol={'6'}
          label={'Last Name'}
          name={'last_name'}
          values={formik.values.last_name}
          onChange={formik.handleChange}
        />

        <SelectInput
          values={formik.values.highschoolGraduation}
          onChange={formik.handleChange}
          name={'highschoolGraduation'}
          label={'When will you finish highschool?'}
          option={finishYearOption}
        />

        <TextIconInput
          icon={<MailOutlineIcon />}
          mdCol={'6'}
          label={'Email'}
          name={'email'}
          values={formik.values.email}
          onChange={formik.handleChange}
        />

        <PasswordInput
          mdCol={'6'}
          label={'Password'}
          name={'password'}
          values={formik.values.password}
          onChange={formik.handleChange}
        />
        <PasswordInput
          mdCol={'6'}
          label={'Password Confirmation'}
          name={'password_confirm'}
          values={formik.values.password_confirm}
          onChange={formik.handleChange}
        />
        {passwordStrength && (
          <Card className="my-2 p-1 border" body>
            <div className="d-flex justify-content-between">
              <span className={`h5 ${passwordStrength.status}`}>{passwordStrength.title}</span>
              <CloseButton onClick={() => setPasswordStrength(undefined)} />
            </div>
            <p className="h6">{passwordStrength.content}</p>
          </Card>
        )}
      </Row>
      <div className="w-100 d-flex align-items-center my-5">
        <div className="border mb-1" style={{ width: 'calc(50% - 20px)' }} />
        <div className="text-muted text-center" style={{ width: '40px' }}>
          OR
        </div>
        <div className="border mb-1" style={{ width: 'calc(50% - 20px)' }} />
      </div>
      <div className="w-100 text-center my-4">
        <GoogleLogin
          clientId="658977310896-knrl3gka66fldh83dao2rhgbblmd4un9.apps.googleusercontent.com"
          buttonText="Create account with Google"
          onSuccess={(responseGoogle) => console.log(responseGoogle)}
          onFailure={(responseGoogle) => console.log(responseGoogle)}
          cookiePolicy={'single_host_origin'}
          style={{ padding: '3rem', borderRadius: '20px' }}
        />
      </div>

      <CheckboxInput
        mdCol={'6'}
        label={
          <span className="mt-2">
            By creating an account you agree with our
            <Button variant="link">terms and conditions.</Button>
          </span>
        }
        name={'password_confirm'}
        values={formik.values.password_confirm}
        onChange={formik.handleChange}
      />
      <Button className="w-25 p-3 m-3" variant="primary" onClick={() => formik.handleSubmit()}>
        <span className="h4">Next</span>
      </Button>
    </Form>
  );
};

export default FormRegister;
